:root {
  --color-bg: #2c2c32;
  --color-active: #0f7dc6;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  background-image: url("https://images.unsplash.com/photo-1470115209269-18dd2d7285cd?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2670&q=80");
  background-size: cover;
  color: #fff;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  min-height: calc(100vh - 85px - 22px);
}

.active {
  color: #0f7dc6;
  color: var(--color-active);
  cursor: default;
}

@media (max-width: 640px) {
  body {
    margin-right: 20px;
    margin-left: 20px;
  }
}

.navigation {
  background-color: transparent;
  list-style: none;
  text-align: center;
  top: 30px;
  padding: 2rem 0;
}

.navigation__link {
  color: var(--color-bg);
  font-weight: 650;
  font-size: 20px;
  margin: 0 10px;
  padding: 15px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s;
  text-align: center;
}

.navigation__link:hover {
  opacity: 0.6;
  z-index: 1;
}

@media (max-width: 640px) {
  .navigation__link {
    font-size: 13px;
    font-weight: 450;
    text-align: center;
    padding: 5px;
  }
}

.home-title {
  margin: 0;
  padding-top: 17rem;
  font-weight: 200;
  font-size: 60px;
  letter-spacing: 5px;
  text-align: center;
  z-index: 1;
}

@media (max-width: 640px) {
  .home-title {
    padding-top: 13rem;
    font-size: 41px;
  }
}

.page__title {
  font-weight: 200;
  font-size: 60px;
  letter-spacing: 5px;
  text-align: center;
  text-shadow: 1px 1px 30px RGB(110, 127, 130);
}

@media (max-width: 640px) {
  .page__title {
    font-size: 30px;
  }
}

.page__title_part {
  display: block;
  position: relative;
  max-width: 1050px;
  margin: 0 auto 20px auto;
  font-weight: 200;
  font-size: 30px;
  letter-spacing: 1px;
  text-align: center;
  color: white;
  text-shadow: 1px 1px 10px RGB(110, 127, 130);
}

.page__subtitle {
  display: block;
  position: relative;
  max-width: 1050px;
  margin: 0 auto 20px auto;
  font-weight: 300;
  font-size: 20px;
  letter-spacing: 1px;
  text-align: center;
  color: RGB(110, 127, 130);
  text-shadow: 1px 1px 20px rgba(255, 255, 255, 0.466);
}

@media (max-width: 640px) {
  .page__subtitle {
    font-size: 13px;
  }
}

.page__iframes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-self: center;
  margin: 50px auto -60px auto;
  grid-column-gap: 50px;
  grid-row-gap: 50px;
}

@media (max-width: 640px) {
  .page__iframes {
    grid-template-columns: repeat(1, 1fr);
    margin: 30px auto -20px auto;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
  }
}

.page__iframe {
  width: 515px;
  height: 316px;
  margin-left: auto;
  border: none;
}

.page__iframe:nth-child(2) {
  margin-right: auto;
  margin-left: 0;
}

.page__iframe:nth-child(4) {
  margin-right: auto;
  margin-left: 0;
}

@media (max-width: 640px) {
  .page__iframe {
    width: 280px;
    height: 210px;
    margin-right: auto;
    margin-left: auto;
  }
  .page__iframe:nth-child(2) {
    margin: auto;
  }
  .page__iframe:nth-child(4) {
    margin: auto;
  }
}

.page__audio {
  display: flex;
  margin-top: 100px;
  justify-content: center;
}

@media (max-width: 640px) {
  .page__audio {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin-top: 100px;
  }
}

.page__audio_song {
  margin: 0 50px 100px 0;
  width: 500px;
  height: 180px;
}

.page__audio_song:last-child {
  margin-right: 0;
}

@media (max-width: 640px) {
  .page__audio_song {
    width: 350px;
    height: 200px;
    margin: 0;
    margin-bottom: 20px;
  }
}

.page__social-icons {
  display: flex;
  justify-content: center;
  list-style: none;
  margin-bottom: 100px;
  padding: 0;
}
.page__social-icons > li {
  margin-left: 50px;
  margin-right: 50px;
}
.page__social-icons a {
  position: relative;
  display: block;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: background-color 0.3s ease-in-out;
  border-radius: 20%;
  width: 70px;
  height: 70px;
}
.page__social-icons a:hover {
  background-color: #e0e0e0;
}
.page__social-icons a::before {
  content: "";
  position: absolute;
  width: 70px;
  height: 70px;
  background: transparent no-repeat center center;
  background-size: cover;
  border-radius: 20%;
}
.page__social-icons .page__social-icon_applemusic::before {
  background-image: url("https://i.pinimg.com/originals/2d/a3/29/2da3293ab201e581488668ab8b7dabd5.png");
}
.page__social-icons .page__social-icon_spotyfy::before {
  background-image: url("https://e7.pngegg.com/pngimages/4/438/png-clipart-spotify-logo-spotify-mobile-app-computer-icons-app-store-music-free-icon-spotify-miscellaneous-logo.png");
}
.page__social-icons .page__social-icon_soundcloud::before {
  background-image: url("https://vectorified.com/images/soundcloud-icon-transparent-10.jpg");
}
.page__social-icons .page__social-icon_youtube::before {
  background-image: url("https://yt3.ggpht.com/ytc/AKedOLRFdev7405zNqBX4TYIsOF4coXVlbus4ngno5qW=s900-c-k-c0x00ffffff-no-rj");
}

.page__social-icons .page__social-icon_github::before {
  background-image: url("https://pngimg.com/uploads/github/github_PNG88.png");
  width: 70px;
  object-fit: contain;
}

@media (max-width: 640px) {
  .page__social-icons {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 40px;
    margin-bottom: 50px;
  }
}

.pulse {
  display: block;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: #fffffe;
  cursor: pointer;
  box-shadow: 0 0 0 rgb(255, 255, 255);
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}

.pulse:hover {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgb(255, 255, 255);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(204, 169, 44, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgb(255, 255, 255);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(204, 169, 44, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
  }
}

.loading {
  color: transparent;
  background: linear-gradient(100deg, #c3c6c9 30%, #b3b4b6 50%, #e5e6e7 70%);
  background-size: cover;
  -webkit-animation: loading 1s ease-in infinite;
          animation: loading 1s ease-in infinite;
}

@-webkit-keyframes loading {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

@keyframes loading {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.it-profile {
  display: flex;
  justify-content: center;
  margin: 50px auto 60px auto;
}

@media (max-width: 640px) {
  .it-profile {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-row-gap: 50px;
    margin-bottom: 50px;
  }
}

.it-profile__img {
  width: 515px;
  height: 330px;
  margin-right: 50px;
  transition: 0.5s;
  display: block;
  object-fit: cover;
}

.it-profile__img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.it-profile__img:last-child {
  margin-right: 0;
}

.it-profile__img:hover {
  opacity: 0.8;
}

@media (max-width: 640px) {
  .it-profile__img {
    width: 350px;
    height: 230px;
    margin: 0;
  }
}

.it-profile__link {
  width: 515px;
  height: 316px;
  margin-right: 50px;
}

.it-profile__link:last-child {
  margin-right: 0;
}

@media (max-width: 640px) {
  .it-profile__link {
    width: 350px;
    height: 230px;
    margin: 0;
  }
}

.contact-span {
  color: white;
  font-weight: 600;
  font-size: 20px;
}



